/* client/src/App.css */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700;900&display=swap');

/* --- FOND ANIMÃ‰ & GLOBAL --- */
body { 
  margin: 0; 
  font-family: 'Poppins', sans-serif;
  color: white; 
  background: linear-gradient(-45deg, #0f0c29, #302b63, #24243e, #1a1a2e);
  background-size: 400% 400%;
  animation: gradientBG 15s ease infinite;
  min-height: 100vh;
  overflow-x: hidden;
}

@keyframes gradientBG {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.App.mobile-layout {
  display: flex; flex-direction: column; min-height: 100vh;
  backdrop-filter: blur(5px); /* Petit effet de flou global */
}

/* --- HEADER --- */
header { 
  padding: 20px; 
  text-align: center; 
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  z-index: 10;
}

h3 { 
  margin: 0; 
  font-weight: 900; 
  letter-spacing: 2px; 
  background: linear-gradient(to right, #ff00cc, #333399); 
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5rem;
}

/* --- CONTENT CONTAINER --- */
.content { 
  flex: 1; padding: 20px; 
  display: flex; flex-direction: column; justify-content: center; 
  max-width: 600px; margin: 0 auto; width: 100%; box-sizing: border-box;
}

/* --- ANIMATIONS D'ENTRÃ‰E --- */
.menu-grid, .lobby-screen, .game-screen {
  animation: slideUp 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

@keyframes slideUp {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

/* --- TITRES ET TEXTES --- */
h2 { font-weight: 700; text-transform: uppercase; margin-bottom: 20px; font-size: 1.8rem; text-shadow: 0 4px 10px rgba(0,0,0,0.5); }
.game-title { font-size: 2.5rem; letter-spacing: 5px; color: #fff; text-shadow: 0 0 20px rgba(255, 255, 255, 0.5); }

/* --- INPUTS --- */
.input-field {
  padding: 18px; 
  background: rgba(255, 255, 255, 0.1); 
  border: 2px solid rgba(255, 255, 255, 0.1); 
  color: white; 
  font-size: 1.2rem; border-radius: 15px; 
  text-align: center; width: 100%; box-sizing: border-box;
  margin-bottom: 15px; transition: all 0.3s ease;
  font-family: 'Poppins', sans-serif;
}
.input-field:focus { outline: none; border-color: #ff00cc; background: rgba(0, 0, 0, 0.4); transform: scale(1.02); }

/* --- BOUTONS MODERNES --- */
button { cursor: pointer; border: none; outline: none; transition: all 0.2s; font-family: 'Poppins', sans-serif; position: relative; overflow: hidden; }
button:active { transform: scale(0.95); }

.big-choice-btn, .game-btn, .join-confirm-btn, .start-btn, .action-btn {
  border-radius: 20px; font-weight: 800; text-transform: uppercase; letter-spacing: 1px;
  box-shadow: 0 10px 20px rgba(0,0,0,0.3);
}

.big-choice-btn { padding: 30px; font-size: 1.4rem; color: white; width: 100%; }
.create { background: linear-gradient(135deg, #ff00cc, #333399); }
.join { background: rgba(255, 255, 255, 0.1); border: 1px solid rgba(255, 255, 255, 0.2); backdrop-filter: blur(5px); }

.game-btn { width: 100%; padding: 25px; font-size: 1.5rem; color: white; margin-bottom: 15px; }
.picolo-btn { background: linear-gradient(135deg, #ff416c, #ff4b2b); }
.undercover-btn { background: linear-gradient(135deg, #11998e, #38ef7d); }

.join-confirm-btn { width: 100%; padding: 20px; background: white; color: black; font-size: 1.2rem; }
.back-btn { background: transparent; color: rgba(255,255,255,0.6); padding: 10px; margin-top: 10px; font-size: 0.9rem; text-decoration: none; }

/* --- LOBBY CHIPS --- */
.big-code { font-size: 4rem; font-weight: 900; color: #fff; letter-spacing: 8px; text-shadow: 0 0 20px #ff00cc; margin: 10px 0; display: block; }
.player-grid { display: flex; flex-wrap: wrap; justify-content: center; gap: 10px; margin: 20px 0; }
.player-chip { 
  background: rgba(255,255,255,0.15); padding: 10px 20px; border-radius: 50px; 
  font-weight: 600; border: 1px solid rgba(255,255,255,0.1); backdrop-filter: blur(5px);
  animation: popIn 0.3s ease forwards;
}
@keyframes popIn { from { transform: scale(0); } to { transform: scale(1); } }

.start-btn { width: 100%; padding: 25px; background: linear-gradient(to right, #f12711, #f5af19); color: white; font-size: 1.5rem; margin-top: 20px; }

/* --- CARTE 3D (FLIP) --- */
.secret-card-container {
  perspective: 1000px; /* Donne la profondeur 3D */
  width: 100%;
  height: 380px;
  margin-top: 20px;
  cursor: pointer;
}

.secret-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s cubic-bezier(0.4, 0.2, 0.2, 1);
  transform-style: preserve-3d;
  border-radius: 20px;
  box-shadow: 0 20px 50px rgba(0,0,0,0.5);
}

.secret-card.revealed .secret-card-inner {
  transform: rotateY(180deg);
}

.card-front, .card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 20px;
  display: flex; flex-direction: column; align-items: center; justify-content: center;
}

/* Face cachÃ©e (Dos de la carte) */
.card-front {
  background: linear-gradient(135deg, #232526, #414345);
  border: 2px solid rgba(255,255,255,0.1);
}
.lock-icon { font-size: 4rem; margin-bottom: 20px; filter: drop-shadow(0 0 10px rgba(255,255,255,0.5)); }

/* Face rÃ©vÃ©lÃ©e (Mot secret) */
.card-back {
  background: white;
  color: #1a1a2e;
  transform: rotateY(180deg);
}
.card-back h1 { font-size: 3rem; margin: 0; color: #ff00cc; text-transform: uppercase; }
.sub-text { color: #666; font-size: 0.9rem; margin-top: 10px; font-weight: bold; }

/* --- VOTE & RESULT --- */
.action-btn { position: fixed; bottom: 30px; left: 5%; width: 90%; padding: 20px; background: #ff00cc; color: white; z-index: 100; font-size: 1.2rem; }

.vote-list { display: flex; flex-direction: column; gap: 12px; margin-top: 20px; }
.vote-btn { 
  background: rgba(255, 255, 255, 0.1); color: white; border: 1px solid rgba(255, 255, 255, 0.1); 
  padding: 20px; font-size: 1.2rem; border-radius: 12px; font-weight: bold; text-align: left;
  display: flex; justify-content: space-between; align-items: center;
}
.vote-btn::after { content: 'ðŸ‘‰'; opacity: 0; transition: opacity 0.2s; }
.vote-btn:active { background: #ff00cc; border-color: #ff00cc; transform: scale(0.98); }
.vote-btn:active::after { opacity: 1; }

.eliminated-name { font-size: 3.5rem; font-weight: 900; color: #ff416c; text-transform: uppercase; margin: 20px 0; text-shadow: 0 0 30px rgba(255, 65, 108, 0.6); animation: shake 0.5s; }
.role-badge { 
  background: white; color: black; padding: 10px 30px; 
  border-radius: 50px; font-weight: 900; font-size: 1.8rem; display: inline-block; 
  box-shadow: 0 0 20px rgba(255,255,255,0.5);
  animation: popIn 0.5s 0.2s backwards;
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-5px) rotate(-5deg); }
  75% { transform: translateX(5px) rotate(5deg); }
}

.pulse { animation: pulse 2s infinite; opacity: 0.6; text-align: center; margin-top: 30px; font-size: 1.1rem; }
@keyframes pulse { 0% { opacity: 0.4; } 50% { opacity: 1; } 100% { opacity: 0.4; } }